import React, { Component } from 'react'
import Page from '../components/Page'
import ProjectCard from '../components/ProjectCard'
import data from '../data/data'
import './Dev.css'

class Dev extends Component {
  constructor (props) {
    super(props)
    this.state = {
      locsData: data.locs,
      locsSlide: data.locs.slides[0],
      slideIndex: data.locs.slides[0].index,
      totalSlides: data.locs.totalSlides
    }
  }

  onChangeHandler = direction => {
    switch (direction) {
      case 'next':
        if (this.state.slideIndex === this.state.totalSlides - 1) {
          this.setState({
            locsSlide: data.locs.slides[0],
            slideIndex: 0
          })
        } else {
          this.setState({
            locsSlide: data.locs.slides[this.state.slideIndex + 1],
            slideIndex: this.state.slideIndex + 1
          })
        }
        break
      case 'prev':
        if (this.state.slideIndex === 0) {
          this.setState({
            locsSlide: data.locs.slides[this.state.locsData.totalSlides - 1],
            slideIndex: this.state.locsData.totalSlides - 1
          })
        } else {
          this.setState({
            locsSlide: data.locs.slides[this.state.slideIndex - 1],
            slideIndex: this.state.slideIndex - 1
          })
        }

        break
    }
  }

  render () {
    return (
      <Page>
        <div className='project-container'>
          <ProjectCard
            title={this.state.locsData.title}
            desc={this.state.locsData.description}
            tools={this.state.locsData.tools}
            totalSlides={this.state.locsData.totalSlides}
            index={this.state.slideIndex}
            slideTitle={this.state.locsSlide.title}
            slideImage={this.state.locsSlide.picture}
            slideDesc={this.state.locsSlide.description}
            change={this.onChangeHandler}
          ></ProjectCard>

          <ProjectCard
            title={this.state.locsData.title}
            desc={this.state.locsData.description}
            tools={this.state.locsData.tools}
            totalSlides={this.state.locsData.totalSlides}
            index={this.state.slideIndex}
            slideTitle={this.state.locsSlide.title}
            slideImage={this.state.locsSlide.picture}
            slideDesc={this.state.locsSlide.description}
            change={this.onChangeHandler}
          ></ProjectCard>
        </div>
      </Page>
    )
  }
}
export default Dev
